import React, {Component} from 'react';
import AlbumList from './../componentes/AlbumList'
import SpotifyWebApi from 'spotify-web-api-js/src/spotify-web-api';
import appConfig from '../AppConfig';

class Home extends Component {

    getHashValue(key) {
        var matches = window.location.hash.match(new RegExp(key + '=([^&]*)'));
        return matches ? matches[1] : null;
    }

    constructor() {
        super();
        this.token = '';
        this.state = {albuns: null};
    }

    componentDidMount() {

        const client_id = '821acfdf1f9e4116ad4a56a4eb8506a8';
        // const client_secret = 'f04bf4b832464e40ad40e1029b0cc738';
        const redirect_uri = appConfig.createBaseUrl();
        let spotifyApi = new SpotifyWebApi();

        if (this.getHashValue('access_token') !== null) {
            this.token = this.getHashValue('access_token');
            spotifyApi.setAccessToken(this.token);
        }

        spotifyApi.getArtistAlbums('3dRfiJ2650SZu6GbydcHNb')
            .then((data) => {
                this.setState({albuns:data.items});
            }, function (err) {
                window.location = 'https://accounts.spotify.com/api/authorize/?client_id=' + client_id + '&redirect_uri=' + redirect_uri + '&response_type=token';
            });
    }

    render() {

        if (!this.state.albuns) {
            return <p>Caregando...</p>;
        }

        return (
            <div>
                <AlbumList albuns={this.state.albuns}/>
            </div>
        );
    }
}

export default Home;



