import React, {Component} from 'react';
import {Card, CardActions, CardMedia, CardText} from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';

import './album-card.css'

class AlbumCard extends Component {
    constructor(props) {
        super(props);

        this.artists = this.props.album.artists[0];
        this.urlImg = this.props.album.images[1].url;
        // console.log(this.props.album);
    }

    render() {
        const nome = this.artists.name;
        const urlImg = this.urlImg;
        return (
                <div className="a-coluna a-25">
                    <Card className="a-card" style={{height: 400}}>
                        <CardMedia>
                            <div className="album-card_img">
                                <img src={urlImg} alt=""/>
                            </div>
                        </CardMedia>
                        <CardText>
                            Artista <br/>
                            Música - {nome}
                        </CardText>
                        <CardActions>
                            <FlatButton label="Play"/>
                        </CardActions>
                    </Card>
            </div>
        );
    }
}

export default AlbumCard;



